import { INews } from '@/type/Types'
import axios from 'axios'

export async function getNewsRoutes(): Promise<INews[]> {
	try {
        const response = await axios.get('http://127.0.0.1:9092/api/news')

        return response.data     
    } catch (error) {
        console.error('Ошибка получения новости:', error)
        throw error
    }
}


export async function getNewsById(id: string): Promise<INews> {
    const ids = parseInt(id, 10)
    try {
        const response = await axios.post(`http://127.0.0.1:9092/api/news/detail?id=${ids}`)

        return response.data     
    } catch (error) {
        console.error('Ошибка получения новости:', error)
        throw error
    }
}